

function stringifyNumWithWidth(num, width) {
    let str = num + ''
    if (num < 0) {
        return str
    }
    if (width == null) {
        return str
    }
    if (width < 2) {
        return str
    }
    for (; str.length < width;) {
        str = '0' + str
    }
    return str
}


/**
  文本化时间戳, ms 表示基于1970-01-01的毫秒数
*/
function StringifyTimestamp(ms) {

    let t = new Date()
    t.setTime(ms)

    let YY = t.getFullYear()
    let MM = t.getMonth() + 1
    let DD = t.getDate()

    let hh = t.getHours()
    let mm = t.getMinutes()
    let ss = t.getSeconds()

    let str = '' + YY
    str += '-' + stringifyNumWithWidth(MM, 2)
    str += '-' + stringifyNumWithWidth(DD, 2)
    str += '  ' + stringifyNumWithWidth(hh, 2)
    str += ':' + stringifyNumWithWidth(mm, 2)
    str += ':' + stringifyNumWithWidth(ss, 2)

    return str
}



export default {
    name: "TimeJS",

    StringifyTimestamp
}
