<template>
  <div>
    <el-table
      :data="items"
      ref="theTable"
      highlight-current-row
      @selection-change="handleSelectionChange"
      @current-change="handleCurrentChange"
    >
      <el-table-column :type="tableColumnType"></el-table-column>

      <el-table-column prop="name" label="名称" sortable>
        <template #default="scope">
          <el-button
            link
            type="primary"
            @click="handleClickItemName(scope.row)"
          >
            {{ scope.row.name }}
          </el-button>
        </template>
      </el-table-column>

      <el-table-column
        prop="content_type"
        label="类型"
        sortable
      ></el-table-column>

      <el-table-column
        prop="updated_at"
        label="修改时间"
        :formatter="formatTimestamp"
        sortable
      ></el-table-column>

      <el-table-column
        prop="size"
        label="大小"
        :formatter="formatByteSize"
        sortable
      ></el-table-column>
    </el-table>
  </div>
</template>

<script>
import TimeJS from "./../../js/time.js";
import ByteJS from "./../../js/bytes.js";

function nop() {}

export default {
  name: "filebrowser-view-detail-table",

  computed: {
    items() {
      return this.$store.getters["files/GetItems"](this.bank);
    },

    tableColumnType() {
      return this.multipleSelection ? "selection" : "index";
      // return "selection";
    },
  },

  data() {
    return {
      selection1: {},
      selectionN: [],
    };
  },

  methods: {
    handleClickItemName(item) {
      if (item.is_dir) {
        this.openDir(item);
      }
    },

    openDir(item) {
      let bank = this.bank;
      let href = item.name;
      this.$store.dispatch("files/fetch", { bank, href });
    },

    getFieldByName(name, item) {
      if (name == null || item == null) {
        return "";
      }
      return item[name];
    },

    handleCurrentChange(currentRow /* , oldCurrentRow */) {
      // let n2 = this.getFieldByName("name", currentRow);
      // let n1 = this.getFieldByName("name", oldCurrentRow);
      // console.warn("current-change old:" + n1 + " current:" + n2);
      ////////
      if (currentRow != null) {
        this.selection1 = currentRow;
      }
      this.fireSelectionChange();
    },

    handleSelectionChange() {
      let rows = this.$refs.theTable.getSelectionRows();
      // let cnt = rows.length;
      // console.warn("selection.count=" + cnt);
      ////////
      if (rows != null) {
        this.selectionN = rows;
      }
      this.fireSelectionChange();
    },

    fireSelectionChange() {
      let items = this.selectionN;
      let item1 = this.selection1;
      let dst = [];
      if (items != null) {
        for (var i in items) {
          dst.push(items[i]);
        }
      }
      if (dst.length == 0 && item1 != null) {
        if (item1.name != null) {
          dst.push(item1);
        }
      }
      this.$emit("selection-change", dst);
    },

    formatByteSize(row, column, cellValue, index) {
      nop(row, column, index);
      return ByteJS.StringifySize(cellValue);
    },

    formatTimestamp(row, column, cellValue, index) {
      nop(row, column, index);
      return TimeJS.StringifyTimestamp(cellValue);
    },
  },

  props: {
    bank: String,
    multipleSelection: Boolean,
  },
};
</script>

