


function tryStringifySizeWithUnit(size, per, unit) {
    let n = size / per
    if (n < 1) {
        return null;
    }
    let str = ''
    if (n < 5) {
        str = n.toFixed(1)
    } else {
        str = n.toFixed(0)
    }
    return str + unit;
}


/**
 * 文本化字节数
*/
function StringifySize(size) {

    const pKB = 1024
    const pMB = 1024 * 1024
    const pGB = 1024 * 1024 * 1024

    let str = tryStringifySizeWithUnit(size, pGB, " GB")
    if (str != null) {
        return str
    }

    str = tryStringifySizeWithUnit(size, pMB, " MB")
    if (str != null) {
        return str
    }

    str = tryStringifySizeWithUnit(size, pKB, " KB")
    if (str != null) {
        return str
    }

    return size + " B"
}



export default {
    name: "BytesJS",

    StringifySize
}
