<template>
  <div>
    <el-dialog
      :modelValue="modelValue"
      @update:modelValue="updateModelValue"
      width="66%"
      :fullscreen="false"
      :title="dialogTitle"
    >
      <MyPathBar :bank="bank" />

      <div class="toolbar toolbar-top">
        <el-button @click="handleClickGoUp">
          <el-icon> <TopLeft /> </el-icon> 向上
        </el-button>

        <el-button @click="handleClickRefresh">
          <el-icon> <Refresh /> </el-icon> 刷新</el-button
        >
        <el-button @click="handleClickMakeDir">
          <el-icon> <Plus /> </el-icon> 新建文件夹</el-button
        >

        <el-input
          class="input-file-name"
          v-model="filename"
          placeholder="文件(或目录)名"
        ></el-input>

        <el-button type="success" @click="handleClickOK">
          {{ actionText }}
        </el-button>

        <el-button type="default" @click="handleClickCancel">取消</el-button>
      </div>

      <MyDetailView :bank="bank" @selection-change="handleSelectionChange" />
      <div class="toolbar toolbar-bottom"></div>
    </el-dialog>
  </div>
</template>

<script setup>
import { Refresh, Plus, TopLeft } from "@element-plus/icons-vue";
</script>

<script>
import MyPathBar from "./path-bar.vue";
import MyDetailView from "./view-detail-table.vue";
import { ElMessageBox } from "element-plus";

export default {
  name: "filebrowser-dialog-common",

  components: { MyPathBar, MyDetailView },

  computed: {
    dialogTitle() {
      let title = this.title;
      if (title != null) {
        if (title != "") {
          return title;
        }
      }
      let s1 = this.actionText;
      let s2 = this.wantText;
      return s1 + s2;
    },

    actionText() {
      let value = this.action;
      return value == "save" ? "保存" : "打开";
    },

    wantText() {
      let value = this.want;
      return value == "folder" || value == "directory" ? "文件夹" : "文件";
    },
  },

  data() {
    return {
      filename: "",
      selectedItems: [],
    };
  },

  methods: {
    refresh() {
      let bank = this.bank;
      this.$store.dispatch("files/fetch", { bank });
    },

    updateModelValue(p) {
      this.$emit("update:modelValue", p);
    },

    handleClickRefresh() {
      this.refresh();
    },

    handleClickGoUp() {
      let bank = this.bank;
      let href = "..";
      this.$store.dispatch("files/fetch", { bank, href });
    },

    handleClickMakeDir() {
      let title = "新建文件夹";
      let text = "请输入文件夹名称";
      ElMessageBox.prompt(text, title, {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
      })
        .then(() => {
          console.warn("todo: mkdir");
        })
        .catch(() => {});
    },

    handleClickCancel() {
      this.updateModelValue(false);
    },

    handleClickOK() {
      this.updateModelValue(false);
      let list = this.selectedItems;
      if (this.action == "save") {
        this.$emit("save-file", list);
      } else {
        this.$emit("open-file", list);
      }
    },

    handleSelectionChange(items) {
      if (items == null) {
        return;
      }
      if (items.length == 1) {
        this.filename = items[0].name;
        this.selectedItems = items;
        return;
      }
      let sep = "";
      let str = "";
      for (var i in items) {
        let it = items[i];
        str += sep + "'" + it.name + "'";
        sep = ", ";
      }
      this.selectedItems = items;
      this.filename = str;
    },

    init() {
      let path = "/";
      let base = "file:///";
      let bank = this.bank;
      this.$store.commit("files/init", { bank, path, base });
    },
  },

  mounted() {
    this.init();
  },

  props: {
    modelValue: Boolean,
    bank: String,
    title: String,
    want: String, // ['file'|'folder']
    action: String, // ['open'|'save']
    multipleSelection: Boolean,
  },

  watch: {
    modelValue(newValue, oldValue) {
      if (newValue == true && oldValue == false) {
        this.refresh();
      }
    },
  },
};
</script>

<style scoped>
.toolbar {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  margin-bottom: 10px;
}

.input-file-name {
  flex-grow: 1;
  margin-left: 10px;
  margin-right: 10px;
}
</style>
