<template>
  <div>
    <MyDialogCommon
      :modelValue="modelValue"
      bank="dialog_open"
      :multipleSelection="multipleSelection"
      @update:modelValue="updateModelValue"
      @open-file="handleOpenFile"
    />
  </div>
</template>

<script>
import MyDialogCommon from "./dialog-common.vue";

export default {
  name: "filebrowser-dialog-open",
  components: { MyDialogCommon },

  methods: {
    updateModelValue(p) {
      this.$emit("update:modelValue", p);
    },

    handleOpenFile(files) {
      this.$emit("open-file", files);
    },
  },

  props: {
    modelValue: Boolean,
    multipleSelection: Boolean,
  },
};
</script>

