<template>
  <div>
    <div class="mode-elements" v-show="mode.current == mode.elements">
      <el-breadcrumb class="mode-elements-bread" :separator-icon="ArrowRight">
        <el-breadcrumb-item>
          <el-button
            class="path-element"
            link
            type="primary"
            @click="handleClickPathRoot"
          >
            根
          </el-button>
        </el-breadcrumb-item>

        <el-breadcrumb-item v-for="item in items" :key="item.id">
          <el-button
            class="path-element"
            link
            type="primary"
            :title="item.path"
            @click="handleClickPathItem(item)"
          >
            {{ item.name }}
          </el-button>
        </el-breadcrumb-item>
      </el-breadcrumb>

      <el-button link @click="handleClickEdit" title="编辑路径">
        <el-icon> <Edit /> </el-icon>
      </el-button>
    </div>

    <div class="mode-input" v-show="mode.current == mode.edit">
      <el-input v-model="pathText" placeholder="请输入要查询的路径">
        <template #prepend>
          <el-button @click="handleClickCancel" title="取消">
            <el-icon> <CloseBold /> </el-icon>
          </el-button>
        </template>

        <template #append>
          <el-button @click="handleClickGo" title="查询">
            <el-icon> <Right /> </el-icon>
          </el-button>
        </template>
      </el-input>
    </div>
  </div>
</template>


<script setup>
import { Edit, Right, CloseBold, ArrowRight } from "@element-plus/icons-vue";
</script>


<script>
// import xxx from ''

export default {
  name: "filebrowser-path-bar",

  computed: {
    items() {
      return this.makeItems(this.elements);
    },

    elements() {
      return this.$store.getters["files/GetPathElements"](this.bank);
    },
  },

  data() {
    return {
      mode: {
        current: 2,
        edit: 1,
        elements: 2,
      },
      pathText: "",
    };
  },

  methods: {
    handleClickEdit() {
      let bank = this.bank;
      let path = this.$store.getters["files/GetPathString"](bank);
      if (path == null) {
        path = "";
      }
      this.pathText = path;
      this.mode.current = this.mode.edit;
    },

    handleClickGo() {
      this.mode.current = this.mode.elements;
      this.navTo(this.pathText);
    },

    handleClickCancel() {
      this.mode.current = this.mode.elements;
    },

    handleClickPathItem(item) {
      let path = item.path;
      this.navTo(path);
    },

    handleClickPathRoot() {
      this.navTo("/");
    },

    makeItems(src) {
      let dst = [];
      let dir = "";
      for (var i in src) {
        let name = src[i];
        let item = this.makeItem(name, dir);
        dst.push(item);
        dir = item.path;
      }
      return dst;
    },

    makeItem(name, parent) {
      let item = {};
      item.path = parent + "/" + name;
      item.name = name;
      return item;
    },

    navTo(path) {
      let bank = this.bank;
      this.$store.dispatch("files/fetch", { bank, path });
    },
  },

  props: { bank: String },
};
</script>

