  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 3px;
  border-style: solid;
  border-width: 1px;
  border-color: rgba(128, 128, 128, 0);
}

.mode-elements:hover {
  background-color: rgb(233, 232, 232);
  border-color: rgb(222, 222, 222);
}

.mode-elements-bread {
  flex-grow: 1;
}

.path-element {
  color: rgb(68, 68, 68);
}

.path-element:hover {
  color: rgb(48, 155, 255);
}
</style>
